.user-img {
    width: 37px;
    border-radius: 50%;
}

.create-post {
    margin-top: 13px;
    background-color: #fff;
    padding: 14px 12px;
    border-radius: 10px;
    display: flex;
    width: 100%;
    flex-direction: column;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.create-post .post {
    display: flex;
    align-items: center;
    gap: 11px;
}

.create-post .post input {
    background-color: #E9EBEE;
    padding: 9px 10px;
    width: 380px;
    outline: none;
    border: none;
    border-radius: 20px;
    font-size: 17px;
}

.box-items {
    display: flex;
    align-items: center;
    justify-content: space-around;
    border-top: 1px solid rgb(224, 220, 220);
    margin-top: 13px;
}

.box-items .box-item {
    display: flex;
    align-items: center;
    gap: 13px;
    margin-top: 20px;
    color: gray;
    font-size: 17px;
    font-weight: 500;
}

.box-items .box-item img {
    width: 30px;
}

.button {
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    transition-duration: 0.4s;
    border-radius: 15px;
}

.button:hover {
    background-color: #45a049;
}

@media only screen and (max-width: 600px) {
    .create-post .post {
        display: flex;
        align-items: center;
        gap: 11px;
        flex-direction: column;
    }

    .create-post {
        padding-left: 5px;
        padding-right: 5px;
        width: 97%;
    }

    .create-post .post input {
        width: 90%;
    }

    .create-post .post img {
        display: none;
    }

    .box-items .box-item:nth-child(2), .box-items .box-item:nth-child(3) {
        display: none;
    }

  }